.arrow {
    position: absolute;
    top: 20%;
    /* width: 75px; */
    width: 5vw;
    opacity: .75;
}

.arrow-left {
    left: 10px;
    animation: left-arrow-anim 2s linear infinite;
}

.arrow-right {
    right: 10px;
    animation: right-arrow-anim 2s linear infinite;
}

.arrow-left:hover {
    opacity: 1;
}

.arrow-right:hover {
    opacity: 1;
}

@keyframes left-arrow-anim {
    0% {
        transform: translateX(0);
    }
    50% {
        transform: translateX(-5px);
    }
    100% {
        transform: translateX(0);
    }
}

@keyframes right-arrow-anim {
    0% {
        transform: translateX(0);
    }
    50% {
        transform: translateX(5px);
    }
    100% {
        transform: translateX(0);
    }
}

.events-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 3px;
    padding: 5px 1em;
    margin-bottom: 1em;
}

.events-header h2 {
    font-size: 2em;
}
.events-header p {
    font-size: 1.5em;
    font-weight: bold;
}

