.spinner:before {
    content: "";
    display: block;
    width: 50px;
    height: 50px;
    border-width: 4px;
    border-style: solid;
    border-radius: 50%;
    border-bottom-color: var(--violet);
    border-right-color: var(--violet);
    border-top-color: var(--blue);
    border-left-color: var(--blue);
    animation: rotate-animation 1s linear 0s infinite;
}

@keyframes rotate-animation {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
}

@keyframes anti-rotate-animation {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(-360deg);
    }
}

.spinner-home {
    width: 60%;
    height: 21vw;
}

.spinner-ev-card {
    width: 18px;
    height: 18px;
}
.spinner-ev-card:before {
    content: "";
    display: block;
    width: 14px;
    height: 14px;
    border-width: 2px;
    border-style: solid;
    border-radius: 50%;
    border-bottom-color: var(--violet);
    border-right-color: var(--violet);
    border-top-color: var(--blue);
    border-left-color: var(--blue);
    animation: rotate-animation 1s linear 0s infinite;
}

@media only screen and (max-width: 1366px) {
    .spinner-home {
        width: 65%;
        height: 22vw;
    }
}

