.w-c {
    width: 100%;
    margin-top: 1.5em;
}
  
.logo-about {
    width: 33%;
    margin-top: 1.5em;
}

.wave-container {
    width: 100%;
    top: -5px;
}

.wave-container:after {
    content: '';
    display: block;
    width: 100%;
    height: 160px;
    background: url(../../assets/images/white-wave.svg) no-repeat;
    position: relative;
    top: 10px;
    background-size: 100%;
}

.wave-container ul {
    display: grid;
    grid-gap: 1.5em;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    margin: 0 1em;
}
  
.wave-container ul li {
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, .1);
    border-radius: 25px;
}
  
.wave-container ul p {
    padding: 0 .5em;
}
  
.wave-container img {
    width: 14%;
}
  
.wave-container ul li:nth-child(3) img {
    width: 8%;
}

.wave-cnt {
    width: 100%;
    height: 212px;
    background: url(../../assets/images/wave.svg) no-repeat;
    background-size: 100%;
}

@media only screen and (min-width: 1500px) {
    .wave-cnt {
        height: 250px;
    }
    .wave-container:after {
        height: 180px;
    }
}

@media only screen and (min-width: 1800px) {
    .wave-cnt {
        height: 300px;
    }
    .wave-container:after {
      height: 200px;
    }
}

@media only screen and (min-width: 2200px) {
    .wave-cnt {
        height: 350px;
    }
    .wave-container:after {
