    }
}

@media only screen and (max-width: 800px) {
    .wave-cnt {
        height: 130px;
    }
    .logo-about {
      width: 50%;
    }
    .wave-container:after {
      height: 110px;
    }
}

@media only screen and (max-width: 700px) {
    .wave-cnt {
        height: 110px;
    }
    .logo-about {
        width: 55%;
    }
    .wave-container:after {
