.blue-card {
    border: 2px solid var(--blue);
}

.align-with-clock {
    margin-left: 4px;
    font-size: 1.05em;
}

.clock-icon {
    width: 20px;
    height: 20px;
}

.ev-more {
    font-size: .9em;
}

.ev-more {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.4s;
}

.ev-more:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -10px;
    transition: 0.5s;
}

.ev-more:hover {
    padding-right: 20px;
}

.ev-more:hover:after {
    opacity: 1;
    right: 0;
}

.lecturer-name {
    margin-bottom: .25em;
    font-size: 1.05em;
}

