.partners {
    margin: 1.2em;
}

.partners > h2 {
    margin-bottom: .15em;
}

.p-c {
    flex-wrap: wrap;
}
  
.hs {
    display: grid;
    grid-gap: 28px;
    grid-template-columns: 1px 250px 250px 250px 1px;
    overflow-x: scroll;
    scroll-snap-type: x proximity;
    padding: 2em 0 2.21em 0;
}

.hs::-webkit-scrollbar {
    display: none;
}

.hs:after {
    content: '';
}
  
.hs:before,
.hs:after {
    content: '';
}