    border: none;
    box-shadow: none;
}
.disabled-btn:after {
    border: none;
    box-shadow: none;
}
.disabled-btn span {
    border: none;
    box-shadow: none;
}
.disabled-btn span {
    border: none;
    box-shadow: none;
} */

@media only screen and (max-width: 1080px) {
    .margin-from-prev-comp {
        margin-top: 6em;
    }
}

@media only screen and (max-width: 800px) {
    .margin-from-prev-comp {
        margin-top: 4em;
    }
}

@media only screen and (max-width: 600px) {
    .margin-from-prev-comp {
        margin-top: 3em;
    }
}